body {
  background-color: black;
  color: white;

  /* height: 58px; */

}

#nav {
  display: flex;
  width: 100%;


}

#ul {
  display: flex;
  width: 100%;
  list-style: none;
  background-color: white;
  padding: 0px;
}

.li {
  padding: 10px;
  margin: 10px;
  text-decoration: underline;
  color: rgba(255, 255, 255, 0.575);
}

a:hover {
  color: rgb(142, 35, 35);
}

.center {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.center textarea {
  font-size: 1rem;
  padding: 10px;
}

.center button {
  font-size: 1rem;
  padding: 10px;
  margin-top: 20px;
  color: white;
  background-color: rgb(255, 2, 200);
}

#sec1 {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  /* height: 300px; */
  /* border: 2px solid red; */
  justify-content: center;
}

#h1 {
  margin: 40px;
  font-size: 40px;
}

#p1 {
  font-size: 20px;
}

#sec2 {
  display: flex;
  /* background-color: white; */
  align-items: center;
  justify-content: center;

}

